import React, { Component } from 'react';
import { Row, Col, Form, Select, Input } from 'antd';
import { BtnSearch, BtnClear, BtnAddNew } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form
        layout="vertical"
        className="ant-advanced-search-form"
        onSubmit={this.handleSearch}
        layout="vertical"
      >
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="H">
              {getFieldDecorator('system1', {})(
                <Select>
                  <Option value="1">i hc chnh quy</Option>
                  <Option value="2">Cao ng chnh quy</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Khoa">
              {getFieldDecorator('system2', {})(
                <Select>
                  <Option value="1">C-in t</Option>
                  <Option value="2">Qun tr kinh doanh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Hc phn">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">Hc phn 1</Option>
                  <Option value="2">Hc phn 2</Option>
                  <Option value="3">Hc phn 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ln thi">
              {getFieldDecorator('system4', {})(
                <Select>
                  <Option value=" 1">Thi ln 1</Option>
                  <Option value=" 2">Thi ln 2</Option>
                  <Option value=" 3">Thi ln 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="t thi">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">Thi t 1</Option>
                  <Option value=" 2">Thi t 2</Option>
                  <Option value=" 3">Thi t 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ti thi s">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">1</Option>
                  <Option value="2">2</Option>
                  <Option value="3">3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="K hiu phch">
              {getFieldDecorator('system7', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="S phch t">{getFieldDecorator('system8', {})(<Input />)}</Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnAddNew title="Thm sinh vin" />
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



